import React from 'react'
import { StyleTela3 } from './Style'

const Tela3 = () => {
  return (
    <StyleTela3>
      <section id='Diferenciais'>
            <div className="Text">

              <div className="title1">
                <h1><main/>Nosso Diferencial <br /></h1>
                <h2>Medidos Multiparamêtros</h2>
              </div>

              <div className="title2">
                <h1><main/>Nosso <br />Diferencial
                Medidor de parâmetro: <br /> Medidos Multiparamêtros</h1>
              </div>
            




            <h3>Os serviços da 123Tele oferecem uma solução única
             no mercado da telemedicina. <br /> Os nossos totens
              e cabines contam com Medidor Multiparâmetros
               , aparelho de alta tecnologia capaz de analisar
                em tempo real as condições de saúde do paciente.</h3>

                 <div className="txtgrup">
                  <div className="txt1">
                    <h4><span>•</span>Pulso</h4>
                    <h4><span>•</span>SpO2 – Oximetria de Pulso</h4>
                    <h4><span>•</span>PA – Pressão Arterial</h4>
                    <h4><span>•</span>Hgb – Hemoglobina</h4>
                    <h4><span>•</span>Hct – Hematócrito</h4>
                    <h4><span>•</span>O2</h4>
                    <h4><span>•</span>CO2</h4>
                    <h4><span>•</span>BV – Viscosidade Sanguínea</h4>
                    <h4><span>•</span>MAP – Pressão Arterial Média</h4>
                    
                  </div>
                  <div className="txt2">
                  <h4><span>•</span>CO - Débito Cardíaco</h4>
                  <h4><span>•</span>SV – Volume Sistólico</h4>
                  <h4><span>•</span>CI – Índice Cardíaco</h4>
                  <h4><span>•</span>PCO2</h4>
                  <h4><span>•</span>PO2</h4>
                  <h4><span>•</span>PH</h4>
                  <h4><span>•</span>RBC – Contagem de Células<br /><b> Vermelhas  (Eritrócitos)</b> </h4>
                  
                  </div>
                </div> 
            </div>
      </section>      
    </StyleTela3>
  )
}

export default Tela3
